<template>
  <div class="container">
    <!-- <Loading :active="isLoading" :z-index="1060"></Loading> -->
    <div class="position-fixed d-block d-flex justify-content-end"
    style="right: 5%; width: 40px">
      <Toast :text="text" ref="toast"/>
      <ToastFail :text="text" ref="toastFail"/>
    </div>
    <table class="table mt-4">
      <thead>
      <tr>
        <th>購買時間</th>
        <th>Email</th>
        <th>購買品項</th>
        <th>應付金額</th>
        <th>是否付款</th>
        <th>編輯</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, key) in orders" :key="key">
        <td> {{ $filters.date(item.create_at) }}</td>
        <td>{{ item.user.email }}</td>
        <td>
           <template v-for="itemDetail in Object.keys(item.products)"
           :key="itemDetail.id">
            <div>
              {{item.products[itemDetail].product.title}} *
              {{item.products[itemDetail].product.num}}
            </div>
          </template>
        </td>
        <td>
          {{item.total}}
          <!-- <span v-if="item.is_enabled === 1" class="text-success">啟用</span> -->
          <!-- <span v-else class="text-muted">未啟用</span> -->
        </td>
        <td>
          <!-- {{item.is_paid}} -->
          <div class="form-check form-switch">
            <input class="form-check-input" type="checkbox" v-model="item.is_paid"
            role="switch" id="flexSwitchCheckChecked" @change="changePaid(item, item.is_paid)">
          </div>
        </td>
        <td>
          <div class="btn-group">
            <button class="btn btn-outline-primary btn-sm"
                    @click="openCouponModal(false, item)"
            >編輯</button>
            <button class="btn btn-outline-danger btn-sm"
                    @click="openDelCouponModal(item)"
            >刪除</button>
          </div>
        </td>
      </tr>
      </tbody>
    </table>
    <OrderModal :is-new="isNew" :order="tempOrder" ref="orderModal"
    @update-order="updateOrder"/>
    <DelModal :item="tempOrder" ref="delModal" @del-item="delOrder"/>
  </div>
</template>
<script>
import OrderModal from '@/components/OrderModal.vue';
import DelModal from '@/components/DeleteModal.vue';
import Toast from '@/components/Toast.vue';
import ToastFail from '@/components/ToastFail.vue';

export default {
  components: {
    OrderModal, DelModal, Toast, ToastFail,
  },
  props: {
    config: Object,
  },
  data() {
    return {
      orders: {},
      tempOrder: {
        title: '',
      },
      isLoading: false,
      isNew: false,
      text: '',
      toastComponent: {},
      toastFailComponent: {},
    };
  },
  methods: {
    openCouponModal(isNew, item) {
      this.isNew = isNew;
      if (this.isNew) {
        this.tempOrder = {
          due_date: new Date().getTime() / 1000,
        };
      } else {
        this.tempOrder = JSON.parse(JSON.stringify(item));
      }
      this.$refs.orderModal.openModal();
    },
    openDelCouponModal(item) {
      this.tempOrder = { ...item };
      console.log('102', this.tempOrder);
      this.$refs.delModal.openModal();
    },
    getOrders() {
      // this.isLoading = true;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/orders`;
      this.$http.get(url).then((response) => {
        console.log(response);
        this.orders = response.data.orders;
      }).catch((error) => {
        console.log(error);
      });
    },
    updateOrder() {
      this.isLoading = true;
      // let url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/order`;
      // let httpMethos = 'post';
      // let data = tempOrder;
      // this.text = '成功新增優惠卷';
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/order/${this.tempOrder.id}`;
      const httpMethos = 'put';
      const data = this.tempOrder;
      this.text = '成功更新訂單';
      this.$http[httpMethos](url, { data }).then((response) => {
        this.isLoading = false;
        this.$refs.orderModal.hideModal();
        console.log(response);
        this.getOrders();
        this.toastComponent.openToast();
        this.toastComponent.autoHideToast();
      }).catch((error) => {
        this.isLoading = false;
        console.log(error.response);
      });
    },
    changePaid(item, item2) {
      console.log(item2);
      this.tempOrder = { ...item };
      const data = this.tempOrder;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/order/${this.tempOrder.id}`;
      this.$http.put(url, { data }).then((response) => {
        console.log(response);
      }).catch((error) => {
        console.log(error.response);
      });
    },
    delOrder() {
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/order/${this.tempOrder.id}`;
      // this.isLoading = true;
      this.$http.delete(url).then((response) => {
        console.log(response);
        const delComponent = this.$refs.delModal;
        delComponent.hideModal();
        this.getOrders();
      }).catch((error) => {
        // this.isLoading = false;
        console.log(error.response);
        // this.$httpMessageState(error.response, '刪除優惠券');
      });
    },
  },
  mounted() {
    this.toastComponent = this.$refs.toast;
    this.toastFailComponent = this.$refs.toastFail;
  },
  created() {
    this.getOrders();
  },
};
// https://courses.hexschool.com/courses/vue-20212/lectures/38620787
</script>

<style lang="scss">
  .form-switch, .form-check-input{
    border-radius: 2rem;
  }
  .form-check-input:checked{
    border-color: #0d6efd;
    background-color: #0d6efd;
  }
</style>
